<template>
  <div class="tools">
    <h1>{{ msg }}</h1>

    <h3>Load an image</h3>
    <br /><br />

    <select v-model="selected">
      <option
        v-for="r in response"
        :key="r.id"
        :value="r.id"
        @change="download()"
      >
        {{ r.name }}
      </option>
    </select>
    <br /><br />
    <button v-on:click="download()">Load</button>
    <br /><br />

    <img class="img-display" style="display: block; margin: 20px auto" />

    <h3>Upload a new image</h3>
    <br /><br />

    <label
      >File
      <input type="file" id="file" ref="file" @change="handleFileUpload()" />
    </label>
    <br /><br />
    <button @click="submitFile()">Submit</button>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Tools",
  props: {
    msg: String,
  },
  data() {
    return {
      file: "",
      response: [],
      errors: [],
    };
  },
  methods: {
    callRestService() {
      axios
        .get(`images`)
        .then((response) => {
          this.response = response.data;
        })
        .catch((e) => {
          this.errors.push(e);
        });
    },
    download() {
      let imageEl = document.querySelector(".img-display");

      axios
        .get(`images/` + this.selected, { responseType: "blob" })
        .then((response) => {
          var reader = new window.FileReader();
          reader.readAsDataURL(response.data);
          reader.onload = function () {
            var imageDataUrl = reader.result;
            imageEl.setAttribute("src", imageDataUrl);
          };
        })
        .catch((e) => {
          this.errors.push(e);
        });
    },
    handleFileUpload() {
      this.file = this.$refs.file.files[0];
    },
    submitFile() {
      let formData = new FormData();
      formData.append("file", this.file);
      axios
        .post("/images", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then(() => {
          this.callRestService();
        })
        .catch((e) => {
          this.errors.push(e);
        });
    },
  },
  mounted,
};

async function mounted() {
  this.callRestService();
  this.download();
}
</script>

