<template>
  <div class="gallery">
    <h1>{{ msg }}</h1>

    <div class="gallery-wrapper">
      <a v-for="r in response" :key="r.id" :value="r.id" class="gallery-content" style="display:block;" :href="'/images/' + r.id">
        <img style="display: inline-block; width: 90px" :src="'/images/' + r.id" />
      </a>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Gallery",
  props: {
    msg: String,
  },
  data() {
    return {
      file: "",
      response: [],
      errors: [],
    };
  },
  methods: {
    callRestService() {
      axios
        .get(`images`)
        .then((response) => {
          this.response = response.data;
        })
        .catch((e) => {
          this.errors.push(e);
        });
    },
    download() {
      let imageEl = document.querySelector(".img-display");

      axios
        .get(`images/` + this.selected, { responseType: "blob" })
        .then((response) => {
          var reader = new window.FileReader();
          reader.readAsDataURL(response.data);
          reader.onload = function () {
            var imageDataUrl = reader.result;
            imageEl.setAttribute("src", imageDataUrl);
          };
        })
        .catch((e) => {
          this.errors.push(e);
        });
    },
    handleFileUpload() {
      this.file = this.$refs.file.files[0];
    },
    submitFile() {
      let formData = new FormData();
      formData.append("file", this.file);
      axios
        .post("/images", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then(() => {
          this.callRestService();
        })
        .catch((e) => {
          this.errors.push(e);
        });
    },
  },
  mounted,
};

async function mounted() {
  this.callRestService();
  this.download();
}
</script>

